/* 1일차 css */
*{
  margin: 0; padding: 0; box-sizing: border-box;
}
a{
  text-decoration: none;
  color: inherit;
}
ul,ol,li{
  list-style: none;
}
img{
  max-width: 100%;;
}
#header{
  position: relative;
  z-index: 10;
  /* up=1100*78, down 1100*44 */
  height: 122px;
  /* background: rgba(255,0,0,0.2); */
}


