#header:hover #logo a{
  background-image: url(/public/images/logo1.png);
}
#header:hover a{
  color: #666;
}
#header:hover{
  background: #fff;
}
#header:hover .util ul li:last-child a{
  background-image: url(/public/images/search1.png);
}
#logo{
  width: 222px;
  height: 42px;
  /* background: #fff; */
  /* padding-top:36px; */
  margin: 18px auto;
  /* padding-top: 18px; */
  text-align: center; 
}
#logo a{
  display: block;
  width: 222px;
  height: 42px;
  background: url(/public/images/logo0.png) no-repeat left top/cover;
}

.util ul{
  display: flex;
  margin-top: 20px;
  font-size: 14px;
}
.util li{
  padding: 0 10px;
  border-right: 1px solid #ccc;
}
.util ul li a{
  color: #fff;
}
.util ul li:last-child{
  border-right: none;
}
.util ul li:last-child a{
  display: block;
  width: 33px; height: 18px;
  background: url(/public/images/search0.png) no-repeat center/auto;
}


.down{
  height: 44px;  
  text-align: center;
  position: relative;
}
#nav>ul{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44px;
}
#nav>ul>li{
  padding: 0 20px;
  /* position: relative; */
}
#nav>ul>li>a{
  position: relative;
  color: #fff;
}
#nav>ul>li>a:after{
  content: "";
  display: block;
  position: absolute;  
  left: 0%;
  bottom: -10px;
  height: 5px;
  width: 0%;
  background: #f00; 
  transition: all .5s;
}
#nav>ul>li>a:hover:after{
   width: 100%;
}
#nav .sub_back{
  position: absolute;
  width: 100%;
  /* padding: 10px 0; */
  background: #fff;
  left: 0;
  bottom: -71px;
  text-align: right;
  display: none;
  z-index: 100;
  padding-bottom: 10px;

}
#nav .sub_back .sub_in{
  width: 1100px;
  margin: 0 auto;
}
#nav .sub_back .sub_in>ul{
  width: 500px;
  float: right;
  text-align: left;
   /* background: rgb(192, 176, 176); */
}
#nav .sub_back .sub_in>ul>li{
  display: inline-block;
}
#nav>ul>li:hover .sub_back{
  display: block;
}
#nav>ul>li:nth-child(6) .sub_back .sub_in>ul{
 margin-right: 45px;
}
#nav>ul>li:nth-child(7) .sub_back .sub_in>ul{
  margin-right: -27px;
}
#nav>ul>li:nth-child(8) .sub_back .sub_in>ul{
  margin-right: -100px;
}


#nav .sub>li>a{
  display: block; padding: 10px 20px; color: #666; 
  margin: 10px 0;
}
#nav>ul>li:nth-child(9) .sub_back{
  bottom: -270px;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul{
  width: 100%;
  text-align: center;  
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul>li{
  margin: 0 80px;
  vertical-align: top;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in>ul>li>a{
  color: #333;
  font-weight: 700;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul ol{
  text-align: left;
  margin-left: 25px;
}
#nav>ul>li:nth-child(9) .sub_back .sub_in ul ol li{
 line-height: 50px;
}
.main{/*670, textIn 1440*670*/
