.start{
  opacity: 0;  
  
}
.end{
  opacity: 1;
 
  transition: all .5s;
}
.rolling{
  position: absolute;
  width: 200px;
  height: 50px;
  left: 210px; bottom: 50px;
}
.rolling ul{
  display: flex;
}
.rolling a{
  display: block;
